.aside{
    position: fixed;
    left : 20px;
    top: 0;
    background: var(--body-color);
    border-right: 1px solid rbga(0,0,0,0.05);
    padding: 2.5 rem;
    width: 90px;
    min-height:100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 10;
}

.nav_list{
    list-style-type: none;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}

.nav_link{
    font-size: 1.5rem;
    color:var(--title-color);
    font-weight: var(--font-bold);
    transition: .3s;
}

.nav_link:hover{
    color:#2a52be;
}

.copyright{
    color: hsl(245, 15%, 65%);
    font-size: var(--small-font-size);
    transform: rotate(-180deg);
    writing-mode: vertical-rl;
}